.resume-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

.resume-viewer {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 10px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .resume-container {
    
    height: 80vh;
    padding: 10px;
    
  }
}
